.preview{
    position: relative;
}
.preview_close{
    position:absolute;
    top:0;
    margin:5px;
    /* font-size:18px; */
    cursor:pointer;
    color:white;
}
.preview_toolbarRight{
    color:white;
    position:absolute;
    right:0;
    display:flex;
    flex-direction:column;
    margin:5px;
}
.preview_toolbarRight > .MuiSvgIcon-root{
    font-size:20px;
    margin-bottom:8px;
    cursor:pointer;
}
.preview_footer{
    position:absolute;
    bottom:0;
    right:-25px;
    transform: translate(-50%,-50%);
    background-color:yellow;
    color:black;
    display:flex;
    justify-content: space-evenly;
    align-items:center;
    border-radius:30px;
    padding:7px;
    cursor:pointer;
}
.preview h2{
    font-size:8px;
    margin-right:3px;
}
.preview_sendIcon{
    font-size:10px;
    
}

